import React from 'react';
import type { Instance, Props as TippyProps } from 'tippy.js';
import 'tippy.js/dist/tippy.css';

import { Tooltip } from './InfoTooltip.styles';

export interface InfoTooltipProps {
  children?: React.ReactNode;
  content: React.ReactNode;
  visible?: boolean;
  className?: string;
  onClick?: React.MouseEventHandler<HTMLDivElement>;
  onShow?: (instance: Instance<TippyProps>) => false | void;
  customAnimation?: 'headShake';
}

export const InfoTooltip = ({
  children,
  content,
  className,
  onClick,
  onShow = () => {},
  visible,
  customAnimation,
}: InfoTooltipProps) => 
  /**
   * @description
   * @tippy.js/react   
   * visible prop   trigger  .
   *  children wrapper event    .
   */
   (
    <Tooltip
      className={className}
      content={<div onClick={onClick}>{content}</div>}
      visible={visible}
      interactive
      arrow
      hideOnClick={false}
      duration={0}
      zIndex={200}
      theme="dcblue"
      placement="bottom" // TODO: bottom-center bottom  ( )
      $customAnimation={customAnimation}
      onShow={onShow}
    >
      <div onClick={onClick}>{children}</div>
    </Tooltip>
  )
;
